@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Lato/Lato-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 700;
  src: url("../../src/fonts/Lato/Lato-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 900;
  src: url("../../src/fonts/Lato/Lato-Black.ttf") format("truetype");
}
@font-face {
  font-family: "Italianno";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Italianno/Italianno-Regular.ttf") format("truetype");
}
[class*=__container] {
  width: min(95%, 120rem);
  margin-inline: auto;
}

.w-100 {
  width: min(100%, 120rem);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  min-width: 32rem;
  font: 400 1.4rem/1.5 "Lato", sans-serif;
}
@media (min-width: 768px) {
  body {
    font: 400 1.6rem/1.5 "Lato", sans-serif;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h1 {
  font: 900 4rem/1.2 "Lato", sans-serif;
}

h2 {
  font: 900 2.4rem/1.2 "Lato", sans-serif;
}

h3 {
  font: 900 2.4rem/1.2 "Lato", sans-serif;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
}

input,
textarea {
  outline: none;
}

.header {
  padding-top: 2.4rem;
}
.header__barra {
  margin-bottom: 2.4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.4rem;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
  }
}
.header__sociales {
  display: flex;
  gap: 0.8rem;
}
.header__social::before {
  content: " ";
  width: 4rem;
  height: 4rem;
  display: inline-block;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.header__social:nth-child(1)::before {
  background-image: url("../img/icono_facebook.svg");
}
.header__social:nth-child(2)::before {
  background-image: url("../img/icono_twitter.svg");
}
.header__social:nth-child(3)::before {
  background-image: url("../img/icono_instagram.svg");
}
.header__social:nth-child(4)::before {
  background-image: url("../img/icono_youtube.svg");
}
.header__social:nth-child(5)::before {
  background-image: url("../img/icono_tiktok.svg");
}
.header__accesible {
  width: 1px;
  height: 1px;
  display: inline-block;
  overflow: hidden;
}
.header__line {
  padding-block: 0.4rem;
  background-color: #8ed3cc;
}
.header__border {
  padding-block: 1.6rem;
  border-block: 2px dashed #FFFFFF;
}
.header__menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
}
@media (min-width: 768px) {
  .header__menu {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__link {
  color: #FFFFFF;
}

.slider {
  padding-top: 2.4rem;
}
.slider__img {
  width: 100%;
}

.bienvenida {
  padding-top: 4.8rem;
}
.bienvenida__titulo {
  margin-bottom: 2.4rem;
  font-family: "Italianno", sans-serif;
  text-align: center;
  font-weight: 400;
  line-height: 0.8;
  color: #e082a8;
}
.bienvenida__titulo span {
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  display: block;
  color: #000;
}
.bienvenida__texto {
  max-width: 24rem;
  margin-inline: auto;
  font-size: 1.6rem;
  text-align: center;
  color: #979797;
}
@media (min-width: 768px) {
  .bienvenida__texto {
    max-width: 40rem;
    font-size: 2rem;
  }
}

.cards {
  padding-top: 2.4rem;
}
.cards__grid {
  display: grid;
  justify-items: center;
}
@media (min-width: 768px) {
  .cards__grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 1.6rem;
  }
}
.cards__card {
  max-width: 35rem;
}
@media (min-width: 768px) {
  .cards__card {
    width: 100%;
    max-width: unset;
  }
}
.cards__img {
  width: 100%;
  border: 0.5rem solid #FFFFFF;
}
@media (min-width: 1024px) {
  .cards__img:hover {
    transform: scale(1.1) rotate(9deg);
    transition: transform 0.3s;
  }
}
.cards__contenido {
  position: relative;
  top: -5rem;
  max-width: 90%;
  margin-inline: auto;
  padding: 0.8rem;
  background-color: #FFFFFF;
}
.cards__heading {
  margin-bottom: 2.4rem;
  font-family: "Italianno", sans-serif;
  text-align: center;
  font-weight: 400;
  line-height: 0.8;
  color: #e082a8;
}
.cards__heading span {
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  display: block;
  color: #000;
}
.cards__enlace {
  width: 100%;
  padding-block: 0.8rem;
  display: block;
  font-size: 1.6rem;
  text-align: center;
  color: #FFFFFF;
  background-color: #e082a8;
}

.horario {
  padding-block: 4rem;
  background-image: linear-gradient(to bottom, #8ed3cc 0%, #8ed3cc 50%, transparent 0%, transparent 50%), url("../img/bg_horarios.jpg");
  background-repeat: no-repeat;
  background-size: 100%, 140rem;
  background-position: center bottom;
}
@supports (background-image: url("../img/bg_horarios.webp")) {
  .horario {
    background-image: linear-gradient(to bottom, #8ed3cc 0%, #8ed3cc 50%, transparent 0%, transparent 50%), url("../img/bg_horarios.webp");
  }
}
@supports (background-image: url("../img/bg_horarios.avif")) {
  .horario {
    background-image: linear-gradient(to bottom, #8ed3cc 0%, #8ed3cc 50%, transparent 0%, transparent 50%), url("../img/bg_horarios.avif");
  }
}
@media (min-width: 768px) {
  .horario {
    background-image: linear-gradient(to right, #8ed3cc 0%, #8ed3cc 50%, transparent 0%, transparent 50%), url("../img/bg_horarios.jpg");
    background-size: 100%, 190rem;
  }
  @supports (background-image: url("../img/bg_horarios.webp")) {
    .horario {
      background-image: linear-gradient(to right, #8ed3cc 0%, #8ed3cc 50%, transparent 0%, transparent 50%), url("../img/bg_horarios.webp");
    }
  }
  @supports (background-image: url("../img/bg_horarios.avif")) {
    .horario {
      background-image: linear-gradient(to right, #8ed3cc 0%, #8ed3cc 50%, transparent 0%, transparent 50%), url("../img/bg_horarios.avif");
    }
  }
}
.horario__grid {
  height: 100%;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  gap: 4rem;
}
@media (min-width: 768px) {
  .horario__grid {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
  }
}
.horario__titulo {
  margin-bottom: 1.6rem;
  font-size: 3.2rem;
  text-align: center;
  color: #FFFFFF;
}
.horario__text {
  margin-bottom: 1.6rem;
  font-size: 2rem;
  text-align: center;
  color: #FFFFFF;
}
.horario__table {
  width: 100%;
  font-size: 1.6rem;
  border-collapse: collapse;
  text-align: center;
  border: 0.5rem solid #e082a8;
  color: #FFFFFF;
}
.horario__thead {
  background-color: #e082a8;
}
.horario__th {
  padding: 0.8rem 1.6rem;
}
@media (min-width: 768px) {
  .horario__tr:hover {
    background-color: #69c5bb;
  }
}
.horario__td {
  padding: 0.8rem 1.6rem;
}

.h-100 {
  height: 100%;
}

.productos {
  padding-block: 2.4rem;
}
@media (min-width: 768px) {
  .productos {
    padding-block: 4.8rem;
  }
}
.productos__titulo {
  margin-bottom: 2.4rem;
  font-family: "Italianno", sans-serif;
  text-align: center;
  font-weight: 400;
  line-height: 0.8;
  color: #e082a8;
  font-size: 4rem;
}
.productos__titulo span {
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  display: block;
  color: #000;
}
@media (min-width: 768px) {
  .productos__titulo {
    margin-bottom: 4.8rem;
  }
}
.productos__grid {
  display: grid;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .productos__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .productos__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.productos__mask {
  overflow: hidden;
  width: 100%;
  display: block;
}
.productos__img {
  width: 100%;
}
.productos__img:hover {
  transform: scale(1.2);
  transition: transform 0.3s;
}
.productos__content {
  padding: 1.6rem;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  background-color: #FFFFFF;
}
.productos__nombre {
  font-size: 2rem;
  color: #e082a8;
}
.productos__precio {
  text-align: center;
  font-size: 2rem;
  color: #979797;
}

.cita {
  padding-block: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.6rem;
  background-image: url("../img/bg_citas.jpg");
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .cita {
    padding-block: 10rem;
  }
}
@media (min-width: 1024px) {
  .cita {
    padding-block: 15rem;
  }
}
.cita__titulo {
  font-size: 3rem;
  font-weight: 400;
}
@media (min-width: 768px) {
  .cita__titulo {
    font-size: 4rem;
  }
}
.cita__texto {
  max-width: 60rem;
  margin-inline: auto;
  line-height: 2;
}
@media (min-width: 768px) {
  .cita__texto {
    font-size: 2rem;
  }
}
.cita__enlace {
  padding: 0.8rem 4.8rem;
  text-align: center;
  font-size: 1.6rem;
  color: #FFFFFF;
  background-color: #8ed3cc;
}

.footer {
  padding: 3.2rem 0 2rem;
  text-align: center;
}
.footer__grid {
  margin-bottom: 1.6rem;
  display: grid;
  gap: 2.4rem;
}
@media (min-width: 768px) {
  .footer__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__nombre {
  font-weight: 700;
  font-size: 2rem;
}
.footer__sociales--footer {
  display: block;
  margin-inline: auto;
}
.footer__copy {
  padding-top: 2rem;
  border-top: 1px solid #d2d2d2;
  color: #4b4b4b;
}